export default angular.module('deskworks.QuickBooksErrors', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer',
])

  .factory ('QuickBooksErrors', function($resource, deskworksConfig, datesDeserializer) {

    const desDates = datesDeserializer(['errors.time']);

    const res = $resource(deskworksConfig.getApiAbsUrl('/quickbooks/centers/:centerId/errors'), {}, {
      query: { method: 'GET', isArray: false, transformResponse: [angular.fromJson, desDates] },
    });

    return {
      query: (centerId, page, pageSize) => {
        return res.query({ centerId, page, pageSize }).$promise;
      },
    };
  });
