export default angular.module('deskworks.QuickBooksProduct', [
  'ngResource',
  'deskworks.config',
])

  .factory('QuickBooksProduct', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/quickbooks/centers/:centerId/products/:productId'), {}, {
      get: { method: 'GET' },
      update: { method: 'PUT' },
    });

    return {
      get: function(centerId, productId) {
        return res.get({ centerId, productId }).$promise;
      },

      save: function(centerId, productId, data) {
        return res.update({ centerId, productId }, data).$promise;
      },
    };
  });
