export default angular.module('deskworks.QuickBooksCenter', [
  'ngResource',
  'deskworks.config',
])

  .factory('QuickBooksCenter', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/quickbooks/centers/:centerId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: function() {
        return res.query().$promise;
      },

      save: function(data) {
        return res.update({ centerId: data.id }, data).$promise;
      },
    };
  });
