import './quick-books-apps.scss';

/**
 * Integrations Setup -> QuickBooks Apps directive
 */
export default angular.module('deskworks.quickBooksApps', [
  'debounce',
  'deskworks.QuickBooksApp',
  'deskworks.QboDepositAccount',
  'deskworks.QboLiabilityAccount',
  'deskworks.QboArAccount',
  'deskworks.QboProduct',
  'deskworks.QboCustomer',
  'deskworks.QboClass',
])

  .directive('quickBooksApps', function() {
    const templateUrl = 'settings/quick-books-apps.tpl.html';

    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'QuickBooksAppsCtrl as $ctrl',
      bindToController: {
        qbApps: '=',
      },
    };
  })

  .controller('QuickBooksAppsCtrl', function($scope, $translate, $q, $filter, $window, dwAlerts, debounce, helper,
                                             centerService, QuickBooksApp, QboDepositAccount, QboLiabilityAccount,
                                             QboArAccount, QboProduct, QboCustomer, QboClass) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    $ctrl.getConnectUrl = app => QuickBooksApp.getConnectUrl(app.id, app.nonce);

    /**
     * Load settings
     */
    $ctrl.load = () => {
      $ctrl.spin = true;

      return QuickBooksApp.query()
        .catch(err => dwAlerts.error(err, 'Failed to query QuickBooks apps.'))
        .finally(() => { $ctrl.spin = false; })
        .then(qbApps => {
          $ctrl.qbApps = qbApps;

          return $q.all($ctrl.qbApps.map(queryAppData));

          function queryAppData(app) {
            app.$$spin = true;

            const promises = {
              qboDepositAccounts: QboDepositAccount.query(app.id)
                .catch(err => dwAlerts.error(err, 'Failed to query QBO deposit accounts.')),
              qboLiabilityAccounts: QboLiabilityAccount.query(app.id)
                .catch(err => dwAlerts.error(err, 'Failed to query QBO liability accounts.')),
              qboArAccounts: QboArAccount.query(app.id)
                .catch(err => dwAlerts.error(err, 'Failed to query QBO ar accounts.')),
              qboProducts: QboProduct.query(app.id)
                .catch(err => dwAlerts.error(err, 'Failed to query QBO products.')),
              qboCustomers: QboCustomer.query(app.id)
                .catch(err => dwAlerts.error(err, 'Failed to query QBO customers.')),
              qboClasses: QboClass.query(app.id)
                .catch(err => dwAlerts.error(err, 'Failed to query QBO classes.')),
            };

            return $q.all(promises)
              .finally(() => { app.$$spin = false; })
              .then(result => {
                app.$$qboDepositAccounts = result.qboDepositAccounts;
                app.$$qboLiabilityAccounts = result.qboLiabilityAccounts;
                app.$$qboArAccounts = result.qboArAccounts;
                app.$$qboProducts = result.qboProducts;
                app.$$qboCustomers = result.qboCustomers;
                app.$$qboClasses = result.qboClasses;

                $ctrl.qbApps.forEach(app => {
                  app.$$qboCustomProduct = helper.find(app.$$qboProducts, app.qboCustomProductId);
                  app.$$qboDiscountProduct = helper.find(app.$$qboProducts, app.qboDiscountProductId);
                  app.$$qboNonMember = helper.find(app.$$qboCustomers, app.qboNonMemberId);
                });
              });
          }
        });
    };

    /**
     * Save QB app
     */
    $ctrl.saveQbApp = debounce(saveQbApp, 500);
    function saveQbApp(app) {
      app.qboCustomProductId = (app.$$qboCustomProduct || {}).id;
      app.qboDiscountProductId = (app.$$qboDiscountProduct || {}).id;
      app.qboNonMemberId = (app.$$qboNonMember || {}).id;

      app.$$spin = true;
      return QuickBooksApp.save(app)
        .finally(() => { app.$$spin = false; })
        .catch(err => dwAlerts.error(err, 'Failed to save QuickBooks app.'));
    }

    /**
     * Delete QB app
     */
    $ctrl.deleteQbApp = function(index) {
      if (!$ctrl.qbApps[index].id) return $ctrl.qbApps.splice(index, 1); // silently remove unsaved app

      if (!$window.confirm($translate.instant('INTEGRATIONS_SETUP.QBO.APP_DELETE_CONFIRMATION'))) return;

      return QuickBooksApp.delete($ctrl.qbApps[index].id)
        .catch(err => dwAlerts.error(err, 'Failed to delete QuickBooks app.'))
        .then(() => {
          $ctrl.qbApps.splice(index, 1);
        });
    };

    /**
     * New QB app
     */
    $ctrl.newQbApp = () => {
      $ctrl.qbApps.push({});
    };

    /**
     * Search items
     */
    $ctrl.search = (items, $search) => {
      return $q.when(items.filter(item => {
        return item.name && item.name.toLowerCase().indexOf($search.toLowerCase()) >= 0;
      }));
    };

    /**
     * Filters results by found search string and sorts by number of matches
     * @param $results - non-filtered search results
     * @param $search - search string
     * @returns {Array.<T>} - filtered results
     */
    $ctrl.filter = ($results, $search) => {
      $results = $results || [];
      $results.forEach(item => {
        item.$$highlighted = { name: $filter('highlight')(item.name, $search) };
      });
      return $results;
    };
  });
