/**
 * Options for Quick Books Errors
 */
export default angular.module('deskworks.optsQuickBooksType', [])

  .factory('optsQuickBooksType', function($rootScope, $translate) {
    const opts = [
      { id: 'item', _name: 'QUICK_BOOKS_ERRORS.OBJ_TYPE.ITEM' },
      { id: 'customer', _name: 'QUICK_BOOKS_ERRORS.OBJ_TYPE.CUSTOMER' },
      { id: 'invoice', _name: 'QUICK_BOOKS_ERRORS.OBJ_TYPE.INVOICE' },
      { id: 'deposit', _name: 'QUICK_BOOKS_ERRORS.OBJ_TYPE.DEPOSIT' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(opt => { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
