/**
 * Product -> QuickBooks account directive
 */
export default angular.module('deskworks.quickBooksAccountDirective', [
  'deskworks.QboIncomeAccount',
  'deskworks.QuickBooksProduct',
  'deskworks.QuickBooksCenter',
])

  .directive('quickBooksAccount', function($compile, $templateCache) {
    const templateUrl = 'account/quick-books-account.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'QuickBooksAccountCtrl as $ctrl',
      bindToController: {
        save: '=',
        productId: '<',
      },
    };
  })

  .controller('QuickBooksAccountCtrl', function($q, dwAlerts, helper, state,
                                                QboIncomeAccount, QuickBooksProduct, QuickBooksCenter) {
    const $ctrl = this;

    $ctrl.$onInit = function() {
      $ctrl.load();
    };

    /**
     * Load data
     */
    $ctrl.load = function() {
      var promises = {
        centers: QuickBooksCenter.query()
          .catch(err => dwAlerts.error(err, 'Failed to query QuickBooks centers.')),
        product: QuickBooksProduct.get(state.getCurrentCenterId(), $ctrl.productId)
          .catch(err => dwAlerts.error(err, 'Failed to load QuickBooks product.')),
      };
      return $q.all(promises)
        .then(result => {
          $ctrl.product = result.product;

          var center = helper.find(result.centers, state.getCurrentCenterId());
          if (center && center.appId && center.enabled) {
            return QboIncomeAccount.query(center.appId)
              .catch(err => dwAlerts.error(err, 'Failed to query QuickBooks income accounts.'))
              .then(accounts => {
                $ctrl.accounts = accounts;
                if ($ctrl.accounts && $ctrl.accounts.length) $ctrl.save = save; // expose save method only if have accounts
              });
          }
        });
    };

    /**
     * Save settings
     */
    function save() {
      return QuickBooksProduct.save(state.getCurrentCenterId(), $ctrl.productId, $ctrl.product)
        .catch(err => dwAlerts.error(err, 'Failed to save QuickBooks product.'));
    }
  });
