import niceTimeDirective from 'shared/nice-date-time/nice-time.directive';

export default angular.module('deskworks.QuickBooksErrorsCtrl', [
  'deskworks.helper',
  'deskworks.dwAlerts',
  'deskworks.QuickBooksErrors',
  'deskworks.optsQuickBooksType',
  niceTimeDirective.name,
])

  .controller('QuickBooksErrorsCtrl', function($scope, $translate, dwAlerts, state, helper,
                                               QuickBooksErrors, optsQuickBooksType) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('QUICK_BOOKS_ERRORS.PAGE_TITLE'));

    /**
     * Fetch log data from server
     */
    $ctrl.fetch = page => {
      $ctrl.fetching = true;
      return QuickBooksErrors.query(state.getCurrentCenterId(), page || $ctrl.data && $ctrl.data.page || 1, 20)
        .catch(err => dwAlerts.error(err, 'Failed to query QuickBooks errors.'))
        .then(result => {
          $ctrl.data = result;
          $ctrl.data.errors.forEach(error => {
            error.$$qbTypeName = (optsQuickBooksType.find(t => t.id === error.qbType) || {}).name || '-';
          });
        })
        .finally(() => { $ctrl.fetching = false; });
    };

    /**
     * Start initial fetching
     */
    $ctrl.fetch();
  });
