export default angular.module('deskworks.QboCustomer', [
  'ngResource',
  'deskworks.config',
])

  .factory('QboCustomer', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/quickbooks/apps/:appId/qbo-customers'), {}, {
      query: { method: 'GET', isArray: true },
    });

    return {
      query: function(appId) {
        return res.query({ appId: appId }).$promise;
      },
    };
  });
