export default angular.module('deskworks.QuickBooksApp', [
  'ngResource',
  'deskworks.config',
])

  .factory('QuickBooksApp', function($resource, $location, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/quickbooks/apps/:appId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: function() {
        return res.query().$promise;
      },

      save: function(app) {
        return app.id ?
          res.update({ appId: app.id }, app).$promise :
          res.save({}, app).$promise
            .then(result => { app.id = result.id; app.nonce = result.nonce; return result; });
      },

      delete: function(appId) {
        return res.delete({ appId: appId }).$promise;
      },

      getConnectUrl: function(appId, nonce) {
        return deskworksConfig.getApiAbsUrl([
          '/quickbooks/oauth2?appId=' + appId,
          'redirectUri=' + encodeURIComponent($location.absUrl()),
          'nonce=' + nonce
        ].join('&'));
      },
    };
  });
