export default angular.module('deskworks.QboClass', [
  'ngResource',
  'deskworks.config',
])

  .factory('QboClass', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/quickbooks/apps/:appId/qbo-classes'), {}, {
      query: { method: 'GET', isArray: true },
    });

    return {
      query: function(appId) {
        return res.query({ appId: appId }).$promise;
      },
    };
  });
