/**
 * Integrations Setup -> QuickBooks Centers directive
 */
export default angular.module('deskworks.quickBooksCenters', [
  'debounce',
  'deskworks.center.service',
  'deskworks.QuickBooksCenter',
])

  .directive('quickBooksCenters', function() {
    const templateUrl = 'settings/quick-books-centers.tpl.html';

    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'QuickBooksCentersCtrl as $ctrl',
      bindToController: {
        qbApps: '<',
      },
    };
  })

  .controller('QuickBooksCentersCtrl', function($scope, $q, $filter, $window, dwAlerts, debounce, helper,
                                                centerService, QuickBooksCenter) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    $ctrl.$onChanges = () => {
      if ($ctrl.qbApps && $ctrl.qbCenters) {
        $ctrl.qbCenters.forEach(center => {
          center.$$app = helper.find($ctrl.qbApps, center.appId);
        });
      }
    };

    /**
     * Load settings
     */

    $ctrl.load = () => {
      $ctrl.spin = true;

      let promises = {
        qbCenters: QuickBooksCenter.query()
          .catch(err => dwAlerts.error(err, 'Failed to query QuickBooks centers.')),
        centers: centerService.getCenters()
          .catch(err => dwAlerts.error(err, 'Failed to query centers.')),
      };

      return $q.all(promises)
        .finally(() => { $ctrl.spin = false; })
        .then(result => {
          $ctrl.qbCenters = result.qbCenters;
          $ctrl.qbCenters.forEach(center => {
            center.name = (helper.find(result.centers, center.id) || {}).name;
            center.$$app = helper.find($ctrl.qbApps, center.appId);
          });
        });
    };

    $ctrl.save = center => {
      center.$$app = helper.find($ctrl.qbApps, center.appId);
      return $ctrl.saveQbCenter(center);
    };

    /**
     * Save QB center
     */
    $ctrl.saveQbCenter = debounce(saveQbCenter, 500);
    function saveQbCenter(center) {
      center.$$spin = true;
      return QuickBooksCenter.save(center)
        .finally(() => { center.$$spin = false; })
        .catch(err => dwAlerts.error(err, 'Failed to save QuickBooks center.'));
    }
  });
